import React, { useEffect } from 'react'
import {motion} from 'framer-motion'
import { ContactContainer, FormGroup } from '../../styles/contactUsStyles'
import { Container, Button, DefaultSizes, VerticalPadding } from '../../styles/globalStyles'

// Context
// import {useGlobalStateContext, useGlobalDispatchContext} from '../../context/globalContext'
//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

import {parent, child} from '../../animations/animations'

const Contact = () => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    return(
        <ContactContainer>
            <Container ref={contentRef} 
                animate={animation} 
                initial='hidden' 
                variants={parent}
            >
                <VerticalPadding>
                    <DefaultSizes black>
                        <motion.h1 variants={child}>Contacte-nos!</motion.h1>
                        <motion.p variants={child}>Entre em contacto connosco e responderemos o mais rapidamente possível.</motion.p>
                    </DefaultSizes>
                
                
                

                <motion.form variants={child}>
                    <FormGroup>
                        <label htmlFor="name">Nome</label>
                        <input type="text" id="name"/>
                    </FormGroup>
                    <FormGroup className="form-group">
                        <label htmlFor="email">Email</label>
                        <input type="text" id="email"/>
                    </FormGroup>
                    <FormGroup className="form-group">
                        <label htmlFor="message">Mensagem</label>
                        <textarea type="text" id="message"></textarea>
                    </FormGroup>
                    <Button>
                        <button id="new-email">Enviar</button>
                    </Button>
                    
                    
                </motion.form>

                </VerticalPadding>
            </Container>
            
            
        </ContactContainer>
    );
}

export default Contact;